import './App.css';
import {useState} from "react";
import datarank from "./photos/datarank.png";

const BASE_URI = "https://word.reacted.xyz";

function getType() {
    const SELECT = document.getElementById("type");
    // eslint-disable-next-line no-unused-expressions
    const SELECTION = SELECT.options[SELECT.selectedIndex].text;
    switch (SELECTION) {
        case "기사":
            return "news";
        case "노래":
            return "music";
        default:
            break;
    }
}

function onSearch() {
    const ENCODE_QUERY = encodeURIComponent(document.getElementById("input").value);
    const TYPE = getType();
    window.location.href = `${BASE_URI}/${TYPE}?query=${ENCODE_QUERY}`;
}

function App() {
    const [width, setwidth]=useState("100%");
    return(
        <div className={"mainbox"}>
            <img src={datarank} width={200}/>
            <div className={"subbox"}>
                <select id={"type"}>
                    <option selected={true}>기사</option>
                    <option>노래</option>
                </select>
                <input id={"input"} style={{width: `${width}`}} onInput={()=>{
                    let value=document.getElementById("input").value;
                    setwidth(value===""?"100%":`${50+12*value.length}px`);
                }} onKeyUp={()=>{
                    if(window.event.keyCode === 13) onSearch();
                }}/>
                <div className={"btn"} onClick={onSearch}>검색</div>
            </div>
        </div>
    )
}

export default App;